 .sidebar_brand {
     display: flex;
     margin: .6rem .1rem;
 }

 .sidebar_brand>span {
     font-size: 1.1rem;
     font-weight: 500;
     margin: 0rem .6rem;
 }

 .sidebar {
     padding: .8rem;
     height: 100vh !important;
     background-color: hsl(231, 48%, 13%, 1);
 }

 .chatview {
     width: 100%;
 }

 .dark .chatview {

     width: 100%;
     height: 100vh;
     background-color: hsl(231, 48%, 13%, 1);
 }

 body.dark .chatview_in {

     border-radius: 10px;
     background-color: #0d1125 !important;
     width: 100%;
     height: 90vh;
 }

 .chatview__chatarea {
     height: 70vh;

 }

 .chatview__textarea-message {
     border: 0px;
 }

 .dark .chatview__textarea-message {
     border: .5px solid hsl(230, 46%, 28%, 0);
     outline: 0px solid transparent;
     background-color: transparent;
     font-size: 16px;
     border: 0px;

 }

 .chatview_in>.form {
     backdrop-filter: blur(10px);
     display: block;
     /* background-color: #ea1010; */
 }

 body.dark .chatview_in>.form>select {
     font-size: 14px;
     font-weight: 500;
     height: 2rem;
     border-radius: .5rem;
     margin: 0 .5rem;
     background-color: hsl(230, 46%, 28%, .3);
     border: .5px solid hsl(230, 46%, 28%, .3);
 }

 .chatview_in>.form>select {
     font-size: 14px;
     font-weight: 500;
     height: 2rem;
     border-radius: .5rem;
     margin: 0 .5rem;
     background-color: hsla(0, 0%, 56%, 0.3);
     border: .5px solid hsla(0, 0%, 56%, 0.3);
 }

 body.dark .chatview__textarea_form {
     /* height: 2rem; */
     margin: .5rem .5rem;
     background-color: hsl(230, 46%, 28%, .3);
     border-radius: .5rem;
     border: .5px solid hsl(230, 46%, 28%, .3);
 }

 .chatview__textarea_form {
     /* height: 2rem; */
     margin: .5rem .5rem;
     background-color: hsla(0, 0%, 63%, 0.3);
     border-radius: .5rem;
     border: 0px solid hsla(0, 0%, 63%, 0.3);
 }

 .chatview__btn-send {
     background-color: transparent;
     border: 0px;

 }

 .nav_asset {

     display: flex;
 }

 .sidebar__btn-close {
     background-color: hsl(230, 46%, 28%, .5);
     font-size: 12px;
     border-radius: 10px;
     border: 0px solid transparent;
     outline: 0px;
     font-weight: 400;
     height: fit-content;
     width: fit-content;
     /* height: 35px; */
     /* width: 20%; */
     padding: .5rem 1rem;
     text-align: center;
     margin-right: .5rem;
 }

 .chatscr_home>.flex_chat {
     height: 100vh;
     width: 100%;
     background-color: #ea1010;
 }

 .chatscr_home {
     height: 100vh;
     /* background-color: aliceblue; */
 }

 .chatscr_home>nav {
     height: 8vh;

 }

 .sidebar {
     height: 90vh;
     /* background-color: aliceblue; */
 }

 .sidebar__app-bar {
     height: 10vh;
     padding: .8rem 0;
     background-color: transparent;
     display: flex;
 }

 .sidebar__app-bar>.sidebar__app-logo {
     width: 80%;
     text-align: center;
 }

 .sidebar__app-bar>.sidebar__app-logo>span {
     color: rgb(147 147 147);
     text-align: center;
     font-size: 14px;
 }

 .sidebar__app-aflogo {
     color: rgb(126, 126, 126);
     margin: 0 1rem;
     width: 22%;
     text-align: center;
     font-size: 14px;
     display: flex;
     flex-direction: row;
     gap: .3rem;
 }

 .logout_sign-out {
     cursor: pointer;
 }

 .message__markdown {
     color: rgb(0, 0, 0);

 }

 body.dark .message__markdown {
     color: white;
 }

 .sidebar__app-aflogo>button {
     padding: .5rem 1rem;
     border-radius: 8px;
     background-color: hsl(230, 46%, 28%, .5);
     /* background-color: rgb(0, 219, 139); */
     color: white;
 }

 .sidebar_brand>img {
     height: 2.5rem;
 }

 .sidebar__app-aflogo>span {
     width: 1rem;
     width: 95%;
     /* background-color: aliceblue; */
 }

 .bg-light-white {
     background-color: hsl(231, 48%, 13%, 1) !important;
 }

 .message {
     background-color: hsl(0, 0%, 88%) !important;
     width: 20%;
 }

 body.dark .message {
     background-color: hsl(231, 48%, 13%, 1) !important;
     width: 20%;
 }