}
.auth__error {
    position: relative;
    background-color: #ff000030;
    /* top: 10%; */
    padding: 0.5rem 0.8rem;
    /* right: 5%; */
    width: fit-content;
    color: #fdfdfd7a;
    margin: 0.5rem auto;
    border-radius: 10px;
}
.auth__error>span {
    font-size: 10px;
    padding: 0 0.2rem;
}
.login-page>.left_log, .register-page>.left_reg{
    background-image: url('../assets/images/log-in-img.png');
    width: 50%;
    height: 100%;
    /* filter: blur(4px);
    -webkit-filter: blur(4px); */
    background-size: contain;
    overflow: scroll !important;
}
.login-page>.left_log>img, .register-page>.left_reg>img {
    width: 80%;
    height: 100%;
}
.login-page>.right_log, .register-page>.right_reg{
    width: 50%;
    height: 100%;
    padding: 5% 3rem;
    background-color: #000521;
    overflow: scroll !important;
}
.login-page>.right_log>.auth, .register-page>.right_reg>.auth{
    background-color: #111631;
    height: fit-content;
    width: 90%;

    margin: auto;
text-align: center;
border-radius: 20px;
padding: 1.5rem 0;
}
.login-page>.right_log>.auth>h1, .register-page>.right_reg>.auth>h1{
    color: rgb(158, 163, 192);
    font-size: 25px;
    font-weight: 600;
    margin: auto;
    padding: .5rem 1rem; 
}
.brnd_link:hover{
    text-decoration: none;
    color: rgb(158, 163, 192);
}
.login-page>.right_log>.auth>h2,
.register-page>.right_reg>.auth>h2{
    color: rgb(228, 228, 229);
    font-size: 18px;
    font-weight: 500;
    margin: auto;
    padding: 1rem;
}
.login-page>.right_log>.auth>form,
.register-page>.right_reg>.auth>form {
    width: 100%;   
    padding: 0 2rem; 
}
.login-page>.right_log>.auth>p,
.register-page>.right_reg>.auth>p{
    color: #6a77c3;
    width: 80%;
    font-size: 15px;
    margin: auto;
}
.donthaveacc{
    padding: .5rem;
}
.donthaveacc>.register_mig{
    color: #6c00ff;
}
.login-page>.right_log>.auth>form>input,
.register-page>.right_reg>.auth>form>input {
background-color: #272d50;
padding: 1rem 1rem;
margin-bottom: 2rem;
width: 100%;
border-radius: 10px;

}
.login-page>.right_log>.auth>form>button,
.register-page>.right_reg>.auth>form>button{
    background-color: #6c00ff;
    padding: .5rem 2rem;
    width: 100%;
    border-radius: 10px;
   
}
.login-page>.right_log>.auth>form>button:hover,
.register-page>.right_reg>.auth>form>button:hover{
     font-weight: 600;
}

